import React, {useEffect,useState} from 'react';
import c3 from "c3";
import "c3/c3.css";
import Slider from './Slider'


export const Chart = () => {

    const [slidernInit,setSlidernInit]=useState(10)
    const [slidernTeams,setSlidernTeams]=useState(5)
    const [sliderpRev,setSliderpRev]=useState(1000000)

    const callbackFunctionnInit=(childData)=>{
        setSlidernInit(childData)
    }
    const callbackFunctionnTeams=(childData)=>{
        setSlidernTeams(childData)
    }
    const callbackFunctionpRev=(childData)=>{
        setSliderpRev(childData)
    }

    const data= [
        {group:0,FT:0,group_label:"Without a design system",Rev:0},
        {group:1,FT:0,group_label:"With a design system",Rev:0}
    ]
  
    useEffect(() => {
        data.map((data,i)=>{
            const nInit=slidernInit;
            const nTeams=slidernTeams;
            const pRev=sliderpRev;
            data.FT=Math.round(nInit/(1-.744*data.group)/nTeams);
            data.Rev=Math.round(data.FT*nTeams*pRev);
            return data
        })
    
    var colors = ['#34495e', '#d44527ff'];
      
        c3.generate({
            bindto: "#chart",
            data: {
                json: data,
                keys:{
                    x:'group_label',
                    value:['FT'] //
                },
                color: function (color, d) {
                    return colors[d.index];
                },
                // colors:{
                //     FT:'#585486'//,//'#D95F02' orange
                //     // Rev:'#35978f' 
                //     //FTwo: '#8c510a'
                // },
                type: 'bar',
                // types: {
                //     'Rev': 'bar'
                // },
                axes: {
                    'Rev': 'y2'
                },
                labels: true
            },
            legend: {
                show: false
            },
            point: {
                show: false
            },
            tooltip: {
                format: {
                    title: '',
                    name: (value)=> {
                        if (value==="Rev") return "Annual revenue";
                        else if (value==="FT") return 'Number of initiatives per team'
                    }
                }},
            axis: {
                rotated: true,
                x: {
                    type: 'category',
                    tick: {
                        outer: false,
                        values: [0, 1]
                    },
                    label: {
                        text: "",
                        position: "inner-center"
                    }
                },
                y: {
                    tick: {
                        outer: false
                    },
                    min: 0,
                    padding : {
                        bottom : 0,
                        top : 50
                    },
                    label: {
                        text: "# of initiatives per team",
                        position: "inner-middle"
                    }
                },
                y2: {
                    tick: {
                        format:  (x)=> { return  '$ '+ x.toLocaleString("en-US")},
                        outer: false
                    },
                    min : 0,
                    padding : {
                        bottom : 0,
                        top : 0
                    },
                    label: {
                        text: "Annual revenue ($MM)",
                        position: "inner-middle"
                    },
                    show: false
                } 
            }
        });
    },[slidernInit,slidernTeams]); //,sliderpRev
  
    return  <>
                <div id="chart" />
                <Slider 
                    parentCallbacknInit={callbackFunctionnInit} 
                    parentCallbacknTeams={callbackFunctionnTeams} 
                    // parentCallbackpRev={callbackFunctionpRev} 
                />
            </>;
};