import React from 'react';
import './SliderChart.css'
import ReactSlider from 'react-slider';


const Slider=(props)=>{
    const sendnInit= (a) => {
        props.parentCallbacknInit(a);
    }
    const sendnTeams= (a) => {
        props.parentCallbacknTeams(a);
    }
    // const sendpRev= (a) => {
    //     props.parentCallbackpRev(a);
    // }
    const initialValues=[
        {id:1,title:"Average number of initiatives per year",change:sendnInit,max:100,min:1,default:10,step:1},    
        {id:2,title:"Number of teams",change:sendnTeams,max:35,min:1,default:5,step:1},
        // {id:3,title:"Average revenue per initiative",change:sendpRev,max:100000000,min:0,default:1000000,step:1000000}   
    ]
    

    return(
        <>
            {initialValues.map((item)=>(
            <div className="sliderWrapper" key={item.id}>
                <span> <p align="left"> {item.title} </p> </span>
                <ReactSlider
                className="horizontal-slider"
                thumbClassName={"example-thumb example-thumb"+item.id}
                trackClassName={" example-track example-track"+item.id}
                onChange={item.change}
                defaultValue={item.default}
                step={item.step}
                max={item.max} 
                min={item.min}
                renderThumb={(props, state) => <div {...props}>{state.valueNow.toLocaleString("en-US")}</div>
                }
                />
            </div>
            ))}

    </>
    )
}

export default Slider;